/**
 * External dependencies
 */
import qs from 'query-string';

/**
 * WordPress dependencies
 */
import { sprintf, __ } from '@wordpress/i18n';
import { useState } from '@wordpress/element';
import { TextControl, Button, Card, CardBody, CardHeader } from '@wordpress/components';

/**
 * Internal dependencies
 */
import StatusIndicator from '@components/StatusIndicator';

const statuses = {
    loading: __('Saving license key...', 'social-image-generator'),
    success: __('License key verified!', 'social-image-generator'),
    error: __('Something went wrong, please try again later.', 'social-image-generator'),
};

const LicensePanel = () => {
    const [status, setStatus] = useState(socialImageGenerator.isValidLicenseKey ? 'success' : 'idle');
    const [statusMessage, setStatusMessage] = useState('');
    const [licenseKey, setLicenseKey] = useState(socialImageGenerator.licenseKey);

    const saveLicenseKey = async () => {
        if (status === 'loading') {
            return;
        }

        setStatus('loading');
        setStatusMessage(null);

        const url = qs.stringifyUrl({
            url: `${socialImageGenerator.apiUrl}/activate-license`,
            query: {
                licenseKey,
            },
        });

        try {
            let response = await (
                await fetch(url, {
                    method: 'POST',
                    headers: {
                        'X-WP-Nonce': socialImageGenerator.nonce,
                    },
                })
            ).json();

            setStatus(response.code === 'success' ? 'success' : 'error');
            setStatusMessage(response.message);
        } catch (e) {
            setStatusMessage(null);
            setStatus('error');
        }
    };

    return (
        <Card className="sig-panel">
            <CardHeader>{__('License', 'social-image-generator')}</CardHeader>
            <CardBody>
                <div className="sig-row">
                    <div className="sig-row__title">{__('License Key', 'social-image-generator')}</div>
                    <div className="sig-row__content">
                        <div className="sig-row__actions">
                            <TextControl
                                style={{ minWidth: '250px' }}
                                value={licenseKey}
                                onChange={(licenseKey) => setLicenseKey(licenseKey)}
                            />
                            <Button
                                className="sig-row__button"
                                isSecondary
                                disabled={status === 'loading' || !licenseKey}
                                onClick={() => {
                                    saveLicenseKey();
                                }}
                            >
                                {__('Save License Key', 'social-image-generator')}
                            </Button>
                        </div>
                        {status !== 'idle' && (
                            <StatusIndicator statuses={statuses} currentStatus={status} message={statusMessage} />
                        )}
                        <div
                            class="sig-row__label"
                            dangerouslySetInnerHTML={{
                                __html: sprintf(
                                    __(
                                        'Enter your license key to receive automatic updates. The license key can be found in the email you received or in the %s.',
                                        'social-image-generator'
                                    ),
                                    '<a target="_blank" href="https://socialimagegenerator.com/license-manager/">License Manager</a>'
                                ),
                            }}
                        ></div>
                    </div>
                </div>
            </CardBody>
        </Card>
    );
};

export default LicensePanel;
