/**
 * WordPress dependencies
 */
import { sprintf } from '@wordpress/i18n';
import { ColorPicker, Popover } from '@wordpress/components';
import { useState } from '@wordpress/element';

const ColorOption = ({ color, label, onChange }) => {
    const [isVisible, setIsVisible] = useState(false);

    return (
        <button className="sig-editor__color-option" onClick={() => setIsVisible(true)} style={{ '--color': color }}>
            {isVisible && (
                <Popover position="bottom left" onClose={() => setIsVisible(false)} noArrow={false}>
                    <ColorPicker color={color} onChangeComplete={(value) => onChange(value.hex)} disableAlpha />
                </Popover>
            )}
            {sprintf('%s Color', label.charAt(0).toUpperCase() + label.slice(1))}
        </button>
    );
};

export default ColorOption;
