/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Button, Card, CardBody, CardHeader } from '@wordpress/components';
import { useState } from '@wordpress/element';

/**
 * Internal dependencies
 */
import Modal from '@components/editor/Modal';
import Preview from '@components/editor/Preview';

const TemplatePanel = () => {
    const template = socialImageGenerator.templates[socialImageGenerator.templateSettings?.template || 'twentytwenty'];

    const [isModalOpen, setIsModalOpen] = useState(false);
    const [settings, setSettings] = useState({
        template: template?.slug || 'twentytwenty',
        colors: socialImageGenerator.templateSettings?.colors || template.colors,
        default_image: socialImageGenerator.templateSettings?.default_image,
        logo: {
            type: socialImageGenerator.templateSettings?.logo?.type || 'title',
            ...socialImageGenerator.templateSettings?.logo,
        },
    });

    return (
        <Card className="sig-panel">
            <CardHeader>{__('Social Images', 'social-image-generator')}</CardHeader>
            <CardBody>
                <div className="sig-row">
                    <div className="sig-row__title">{__('Template', 'social-image-generator')}</div>
                    <div className="sig-row__content">
                        {isModalOpen && (
                            <Modal
                                settings={settings}
                                onClose={() => setIsModalOpen(false)}
                                onChange={(settings) => {
                                    setSettings(settings);
                                }}
                            />
                        )}
                        <div className="sig-preview-wrapper">
                            <Preview
                                template={settings.template}
                                logo={settings.logo}
                                colors={settings.colors}
                                defaultImage={settings?.default_image?.url}
                                editable={false}
                                hasImage={Boolean(settings.default_image && settings.default_image.url)}
                            />
                            <div className="sig-preview-wrapper__caption">
                                <span className="sig-preview-wrapper__title">
                                    {__('Example Post Title', 'social-image-generator')}
                                </span>
                                <span className="sig-preview-wrapper__url">{socialImageGenerator.siteLink}</span>
                            </div>
                        </div>
                        <div className="sig-row__actions">
                            <Button className="sig-row__button" isSecondary onClick={() => setIsModalOpen(true)}>
                                {__('Edit Template', 'social-image-generator')}
                            </Button>
                        </div>
                    </div>
                </div>
            </CardBody>
        </Card>
    );
};

export default TemplatePanel;
