/**
 * External dependencies
 */
import qs from 'query-string';

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { Modal, RadioControl, Button, Popover, SlotFillProvider, TextControl } from '@wordpress/components';
import { useState, useRef, useEffect } from '@wordpress/element';
import { MediaUpload } from '@wordpress/media-utils';

/**
 * Internal dependencies
 */
import StatusIndicator from '@components/StatusIndicator';
import Preview from './Preview';
import TemplatePickerItem from './TemplatePickerItem';
import ColorOption from './ColorOption';

const ALLOWED_MEDIA_TYPES = ['image/jpeg', 'image/png'];

const statuses = {
    loading: __('Saving changes...', 'social-image-generator'),
    success: __('Changes saved successfully!', 'social-image-generator'),
    error: __('Something went wrong, please try again later.', 'social-image-generator'),
};

const EditorModal = ({ settings, onChange, onClose }) => {
    const [width, setWidth] = useState(settings?.logo?.width || 50);
    const [defaultImage, setDefaultImage] = useState(settings?.default_image || null);
    const [logoType, setLogoType] = useState(settings?.logo?.type || 'title');
    const [logoText, setLogoText] = useState(settings?.logo?.text || '');
    const [logoImage, setLogoImage] = useState(settings?.logo || null);
    const [isTemplatePickerOpen, setIsTemplatePickerOpen] = useState(false);
    const [status, setStatus] = useState('idle');
    const [editorTemplate, setEditorTemplate] = useState(
        settings?.template
            ? socialImageGenerator.templates[settings.template]
            : socialImageGenerator.templates['twentytwenty']
    );
    const [colors, setColors] = useState(settings?.colors || editorTemplate.colors);
    const [titleText, setTitleText] = useState(null);

    const titleInput = useRef(null);
    const modal = useRef(null);
    const templatePicker = useRef(null);

    useEffect(() => {
        setTitleText(titleInput.current.textContent);

        titleInput.current.addEventListener('input', () => {
            setTitleText(titleInput.current.textContent);
        });
    }, []);

    useEffect(() => {
        for (const [label, color] of Object.entries(colors)) {
            modal.current.style.setProperty(`--color-${label}`, color);
        }
    }, [colors]);

    useEffect(() => {
        if (defaultImage) {
            modal.current.style.setProperty('--default-image', `url(${defaultImage.url})`);
        } else {
            modal.current.style.removeProperty('--default-image');
        }
    }, [defaultImage]);

    const saveSettings = async () => {
        if (status === 'loading') {
            return;
        }

        setStatus('loading');

        const url = qs.stringifyUrl({
            url: `${socialImageGenerator.defaultApiUrl}/settings`,
        });

        const newSettings = {
            template: editorTemplate.slug,
            colors,
            logo: {
                type: logoType,
                ...(logoImage ? { id: logoImage.id, width } : {}),
                ...(logoType === 'text' ? { text: logoText } : {}),
            },
            ...(defaultImage ? { default_image: defaultImage } : {}),
        };

        try {
            let response = await fetch(url, {
                method: 'POST',
                headers: {
                    'X-WP-Nonce': socialImageGenerator.nonce,
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    sig_template_settings: newSettings,
                }),
            });

            setStatus(response.status === 200 ? 'success' : 'error');

            if (response.status === 200) {
                onChange({ ...newSettings, logo: { url: logoImage.url, ...newSettings.logo } });
            }
        } catch (e) {
            setStatus('error');
        }

        setTimeout(() => {
            setStatus('idle');
        }, 4000);
    };

    return (
        <SlotFillProvider>
            <Popover.Slot />
            <Modal
                title={__('Edit Template', 'social-image-generator')}
                onRequestClose={() => onClose()}
                shouldCloseOnClickOutside={false}
            >
                <div className="sig-editor" ref={modal} data-status={status}>
                    <div className="sig-preview-wrapper">
                        <Preview
                            template={editorTemplate.slug}
                            logo={{
                                type: logoType,
                                text: logoText,
                                image: logoImage,
                                url: logoImage.url,
                                width,
                                setWidth,
                            }}
                            defaultImage={defaultImage?.url}
                            editable={true}
                            titleRef={titleInput}
                            hasImage={Boolean(defaultImage && defaultImage.url)}
                        />
                        <div className="sig-preview-wrapper__caption">
                            <span className="sig-preview-wrapper__title">{titleText}</span>
                            <span className="sig-preview-wrapper__url">{socialImageGenerator.siteLink}</span>
                        </div>
                    </div>
                    <div className="sig-editor__controls">
                        <div className="sig-editor__control sig-editor__control--template">
                            <img
                                className="sig-editor__template-preview"
                                src={`${socialImageGenerator.imgUrl}thumbnails/${editorTemplate.slug}.jpg`}
                            />
                            <div>
                                <strong>{editorTemplate.name}</strong>
                            </div>
                            <Button
                                isSecondary
                                onClick={() => {
                                    setIsTemplatePickerOpen(true);

                                    setTimeout(() => {
                                        modal.current.parentElement.parentElement.scrollTo({
                                            top: templatePicker.current.offsetTop - 60,
                                            behavior: 'smooth',
                                        });
                                    }, 0);
                                }}
                            >
                                {__('Change Template', 'social-image-generator')}
                            </Button>
                        </div>
                        <div className="sig-editor__control">
                            <h3>{__('Logo', 'social-image-generator')}</h3>
                            <RadioControl
                                selected={logoType}
                                options={[
                                    { label: __('Site Title', 'social-image-generator'), value: 'title' },
                                    { label: __('Site Link', 'social-image-generator'), value: 'link' },
                                    { label: __('Image', 'social-image-generator'), value: 'image' },
                                    { label: __('Text', 'social-image-generator'), value: 'text' },
                                    { label: __('No Logo', 'social-image-generator'), value: 'none' },
                                ]}
                                onChange={(type) => setLogoType(type)}
                            />
                            {logoType === 'image' && (
                                <MediaUpload
                                    onSelect={(media) => {
                                        setLogoImage(media);
                                        setWidth(50);
                                    }}
                                    allowedTypes={ALLOWED_MEDIA_TYPES}
                                    render={({ open }) => (
                                        <p>
                                            <Button isSecondary onClick={open}>
                                                {logoImage
                                                    ? __('Change Logo', 'social-image-generator')
                                                    : __('Choose Logo', 'social-image-generator')}
                                            </Button>
                                        </p>
                                    )}
                                />
                            )}
                            {logoType === 'text' && (
                                <TextControl
                                    value={logoText}
                                    label={__('Logo Text', 'social-image-generator')}
                                    onChange={(text) => setLogoText(text)}
                                />
                            )}
                        </div>
                        <div className="sig-editor__control">
                            <h3>{__('Colors', 'social-image-generator')}</h3>
                            {Object.entries(colors).map(([label, color]) => {
                                if (label === 'logo' && ['image', 'none'].includes(logoType)) {
                                    return <></>;
                                }

                                return (
                                    <ColorOption
                                        label={label}
                                        color={color}
                                        onChange={(newColor) => setColors({ ...colors, [label]: newColor })}
                                    />
                                );
                            })}
                        </div>
                        {editorTemplate.image && (
                            <div className="sig-editor__control">
                                <h3>{__('Default Image', 'social-image-generator')}</h3>
                                <div className="sig-editor__default-image-preview">
                                    {defaultImage && defaultImage.url && <img src={defaultImage.url} />}
                                    <div>
                                        {__(
                                            'By default, this template will use the featured image of a post or page. You can define a default image that can be used when the post or page does not have a featured image.',
                                            'social-image-generator'
                                        )}
                                        <MediaUpload
                                            onSelect={(media) =>
                                                setDefaultImage({
                                                    id: media.id,
                                                    url: media.url,
                                                })
                                            }
                                            allowedTypes={ALLOWED_MEDIA_TYPES}
                                            render={({ open }) => (
                                                <Button isSecondary onClick={open}>
                                                    {defaultImage
                                                        ? __('Change Default Image', 'social-image-generator')
                                                        : __('Choose Default Image', 'social-image-generator')}
                                                </Button>
                                            )}
                                        />
                                        {defaultImage && (
                                            <Button isLink isDestructive onClick={() => setDefaultImage(null)}>
                                                {__('Remove Default Image', 'social-image-generator')}
                                            </Button>
                                        )}
                                    </div>
                                </div>
                            </div>
                        )}
                    </div>
                    <div className="sig-editor__footer">
                        <Button isPrimary onClick={() => saveSettings()} isBusy={status === 'loading'}>
                            {__('Save Changes', 'social-image-generator')}
                        </Button>
                        {status !== 'idle' && <StatusIndicator statuses={statuses} currentStatus={status} />}
                    </div>
                    {isTemplatePickerOpen && (
                        <div className="sig-editor__template-picker" ref={templatePicker}>
                            {Object.values(socialImageGenerator.templates).map((template) => (
                                <TemplatePickerItem
                                    name={template.name}
                                    slug={template.slug}
                                    active={template.slug === editorTemplate.slug}
                                    onClick={() => {
                                        setEditorTemplate(template);
                                        setColors(template.colors);
                                        modal.current.parentElement.parentElement.scrollTo(0, 0);
                                        setIsTemplatePickerOpen(false);
                                    }}
                                />
                            ))}
                        </div>
                    )}
                </div>
            </Modal>
        </SlotFillProvider>
    );
};

export default EditorModal;
