/**
 * WordPress dependencies
 */
import { Spinner } from '@wordpress/components';
import { Icon, check, close } from '@wordpress/icons';

const StatusIndicator = ({ statuses, currentStatus, message }) => (
    <span className={`sig-status sig-status--${currentStatus}`}>
        {currentStatus === 'loading' ? (
            <Spinner />
        ) : (
            <Icon size={18} icon={currentStatus === 'success' ? check : close} />
        )}
        {message || statuses[currentStatus]}
    </span>
);

export default StatusIndicator;
