/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { ResizableBox } from '@wordpress/components';

const Preview = ({ template, logo, editable, colors = {}, defaultImage = null, titleRef = null, hasImage = false }) => {
    const style = {};

    if (colors) {
        for (const [label, color] of Object.entries(colors)) {
            style[`--color-${label}`] = color;
        }
    }

    if (defaultImage) {
        style[`--default-image`] = `url(${defaultImage})`;
    }

    return (
        <div className="sig-preview" data-template={template} style={style} data-image={hasImage}>
            <span className="sig-preview__logo" data-type={logo.type}>
                {logo.type === 'title' && <>{socialImageGenerator.siteTitle}</>}
                {logo.type === 'link' && <>{socialImageGenerator.siteLink}</>}
                {logo.type === 'text' && <>{logo.text}</>}
                {logo.type === 'image' &&
                    logo.url &&
                    logo.width &&
                    (editable ? (
                        <ResizableBox
                            className="sig-preview__logo-img"
                            size={{
                                width: logo.width,
                                height: logo.width * (logo.image.height / logo.image.width),
                            }}
                            style={{
                                display: 'flex',
                                alignItems: 'stretch',
                                justifyContent: 'stretch',
                            }}
                            minHeight="25"
                            minWidth="25"
                            maxHeight="500"
                            maxWidth="1100"
                            showHandle={editable}
                            lockAspectRatio={true}
                            enable={{
                                top: false,
                                right: false,
                                bottom: false,
                                left: false,
                                topRight: false,
                                bottomRight: editable,
                                bottomLeft: false,
                                topLeft: false,
                            }}
                            onResizeStop={(event, direction, elt, delta) => {
                                logo.setWidth(parseInt(logo.width + delta.width, 10));
                            }}
                        >
                            <img src={logo.url} />
                        </ResizableBox>
                    ) : (
                        <img src={logo.url} style={{ width: logo.width }} />
                    ))}
            </span>
            <p className="sig-preview__post-title" contentEditable={editable} spellcheck="false" ref={titleRef}>
                {__('Example Post Title', 'social-image-generator')}
            </p>
        </div>
    );
};

export default Preview;
