export default () => {
    jQuery(function ($) {
        $('body').on('click', '[data-sig-classic-editor-set-image]', function (e) {
            e.preventDefault();

            var sig_uploader = wp
                .media({
                    library: {
                        type: 'image',
                    },
                    multiple: false,
                })
                .on('select', function () {
                    var attachment = sig_uploader.state().get('selection').first().toJSON();
                    $('#sig_custom_image').val(attachment.id);
                    $('[data-sig-classic-editor-image-preview]').attr('src', attachment.url).removeAttr('hidden');
                    $('[data-sig-classic-editor-remove-image]').removeAttr('hidden');
                })
                .open();
        });

        $('body').on('click', '[data-sig-classic-editor-remove-image]', function (e) {
            e.preventDefault();

            $('#sig_custom_image').val(0);
            $('[data-sig-classic-editor-image-preview]').attr('src', '').attr('hidden', true);
            $(this).attr('hidden', true);
        });

        $('body').on('change', 'select[name="sig_image_type"]', function (e) {
            $('[data-sig-classic-editor-custom-image]').attr('hidden', function () {
                return e.target.value !== 'custom-image';
            });
        });

        $('body').on('change', 'input[name="sig_is_disabled"]', function (e) {
            $('.sig-classic-editor').toggleClass('sig-classic-editor--disabled', e.target.checked);
        });
    });
};
