/**
 * External dependencies
 */
import qs from 'query-string';

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { compose } from '@wordpress/compose';
import { MediaUpload, MediaUploadCheck } from '@wordpress/block-editor';
import { ExternalLink, TextControl, ToggleControl, Button, SelectControl, Spinner } from '@wordpress/components';
import { withSelect, withDispatch } from '@wordpress/data';
import { useState, useEffect } from '@wordpress/element';
import { registerPlugin } from '@wordpress/plugins';
import { PluginDocumentSettingPanel } from '@wordpress/edit-post';

const template = socialImageGenerator.templates[socialImageGenerator.templateSettings?.template || 'twentytwenty'];

const applyWithSelect = withSelect((select) => {
    const imageId = select('core/editor').getEditedPostAttribute('featured_media');
    const customImageId = select('core/editor').getEditedPostAttribute('meta')['sig_custom_image'];

    return {
        postId: select('core/editor').getCurrentPostId(),
        title: select('core/editor').getPostEdits().title || select('core/editor').getCurrentPost().title,
        image: imageId ? select('core').getMedia(imageId) : null,
        imageType: select('core/editor').getEditedPostAttribute('meta')['sig_image_type'],
        customImage: customImageId ? select('core').getMedia(customImageId) : null,
        customText: select('core/editor').getEditedPostAttribute('meta')['sig_custom_text'],
        isDisabled: select('core/editor').getEditedPostAttribute('meta')['sig_is_disabled'],
    };
});

const applyWithDispatch = withDispatch((dispatch) => {
    return {
        setImageType: (value) => {
            dispatch('core/editor').editPost({ meta: { sig_image_type: value } });
        },
        setCustomText: (value) => {
            dispatch('core/editor').editPost({ meta: { sig_custom_text: value } });
        },
        setCustomImage: (value) => {
            dispatch('core/editor').editPost({ meta: { sig_custom_image: value } });
        },
        setIsDisabled: (value) => {
            dispatch('core/editor').editPost({ meta: { sig_is_disabled: value } });
        },
    };
});

const Panel = ({
    postId,
    title,
    image,
    customText,
    setCustomText,
    isDisabled,
    setIsDisabled,
    imageType,
    setImageType,
    customImage,
    setCustomImage,
}) => {
    const ALLOWED_MEDIA_TYPES = ['image/jpeg', 'image/png'];
    const [generatedImageUrl, setGeneratedImageUrl] = useState('');
    const [isLoading, setIsLoading] = useState(true);
    const [currentTitle, setCurrentTitle] = useState(title);
    const defaultImage = socialImageGenerator.templateSettings?.default_image?.url || null;

    useEffect(() => {
        const skipUpdate = currentTitle !== title && !!customText;

        setCurrentTitle(title);

        if (skipUpdate) {
            return;
        }

        setIsLoading(true);

        const handler = setTimeout(() => {
            let imageUrl = null;

            if (imageType === 'featured-image') {
                imageUrl = image ? image.media_details?.sizes?.large?.source_url || image.source_url : null;

                if (!imageUrl) {
                    imageUrl = defaultImage;
                }
            }

            if (imageType === 'custom-image' && customImage) {
                imageUrl = customImage.media_details?.sizes?.large?.source_url || customImage.source_url;
            }

            if (imageType === 'default-image') {
                imageUrl = defaultImage;
            }

            setGeneratedImageUrl(
                qs.stringifyUrl(
                    {
                        url: `${socialImageGenerator.apiUrl}/generator`,
                        query: {
                            _wpnonce: socialImageGenerator.nonce,
                            text: customText || title,
                            image: imageUrl,
                            postId,
                            cacheBuster: Math.round(new Date().getTime() / 1000),
                        },
                    },
                    { skipNull: true }
                )
            );
        }, 2000);

        return () => {
            clearTimeout(handler);
        };
    }, [title, customText, imageType, customImage, image]);

    return (
        <PluginDocumentSettingPanel
            name="social-image-generator"
            title={__('Social Image Preview', 'social-image-generator')}
            className="sig-sidebar"
        >
            {!isDisabled && (
                <>
                    <div className="sig-sidebar__preview-container">
                        {isLoading && (
                            <div className="sig-sidebar__preview-loader">
                                <Spinner />
                            </div>
                        )}
                        {generatedImageUrl && (
                            <img
                                className="sig-sidebar__preview"
                                src={generatedImageUrl}
                                onLoad={() => setIsLoading(false)}
                            />
                        )}
                    </div>
                    <hr />
                    <TextControl
                        value={customText}
                        onChange={(value) => setCustomText(value)}
                        label={__('Custom Text', 'social-image-generator')}
                        help={__(
                            'By default the post title is used for the image. You can use this field to set your own text.',
                            'social-image-generator'
                        )}
                    />
                    <hr />
                    {template.image && (
                        <>
                            <SelectControl
                                label={__('Image Type', 'social-image-generator')}
                                value={imageType}
                                options={[
                                    { label: __('Featured Image', 'social-image-generator'), value: 'featured-image' },
                                    { label: __('Custom Image', 'social-image-generator'), value: 'custom-image' },
                                    { label: __('Default Image', 'social-image-generator'), value: 'default-image' },
                                    { label: __('No Image', 'social-image-generator'), value: 'no-image' },
                                ]}
                                onChange={(type) => {
                                    setImageType(type);
                                }}
                                help={
                                    imageType === 'default-image'
                                        ? __(
                                              'You can change the default image by clicking the Edit link below.',
                                              'social-image-generator'
                                          )
                                        : null
                                }
                            />
                            {imageType === 'custom-image' && (
                                <div className="sig-sidebar__custom-image">
                                    <MediaUploadCheck>
                                        <MediaUpload
                                            onSelect={(media) => {
                                                setCustomImage(media.id);
                                            }}
                                            allowedTypes={ALLOWED_MEDIA_TYPES}
                                            render={({ open }) => (
                                                <>
                                                    {customImage && (
                                                        <img
                                                            src={
                                                                customImage.media_details?.sizes?.large?.source_url ||
                                                                customImage.source_url
                                                            }
                                                            onClick={open}
                                                        />
                                                    )}
                                                    <div>
                                                        <Button isPrimary onClick={open}>
                                                            {customImage
                                                                ? __('Replace Custom Image', 'social-image-generator')
                                                                : __('Choose Custom Image', 'social-image-generator')}
                                                        </Button>
                                                        {customImage && (
                                                            <Button
                                                                isLink
                                                                isDestructive
                                                                onClick={() => setCustomImage(null)}
                                                            >
                                                                {__('Remove Custom Image', 'social-image-generator')}
                                                            </Button>
                                                        )}
                                                    </div>
                                                </>
                                            )}
                                        />
                                    </MediaUploadCheck>
                                </div>
                            )}
                            <hr />
                        </>
                    )}
                </>
            )}

            <ToggleControl
                label={__('Disable Social Image', 'social-image-generator')}
                help={isDisabled ? __('Social Image is disabled for this post.', 'social-image-generator') : ''}
                checked={isDisabled}
                onChange={() => setIsDisabled(!isDisabled)}
            />
            <hr />
            <ExternalLink href="options-general.php?page=social-image-generator">
                {__('Edit Social Image Template', 'social-image-generator')}
            </ExternalLink>
        </PluginDocumentSettingPanel>
    );
};

export default () => {
    registerPlugin('social-image-generator', {
        render: compose(applyWithSelect, applyWithDispatch)(Panel),
        icon: false,
    });
};
