/**
 * WordPress dependencies
 */
import { Button } from '@wordpress/components';
import { Icon, check } from '@wordpress/icons';

const TemplatePickerItem = ({ name, slug, onClick, active }) => (
    <Button className="sig-editor__template-picker-item" onClick={onClick} disabled={active} data-active={active}>
        {active && (
            <span className="sig-editor__template-picker-icon">
                <Icon size={18} icon={check} />
            </span>
        )}
        <p className="sig-editor__template-picker-title">{name}</p>
        <img
            width="1200"
            height="630"
            className="sig-editor__template-picker-preview"
            src={`${socialImageGenerator.imgUrl}thumbnails/${slug}.jpg`}
        />
    </Button>
);

export default TemplatePickerItem;
