/**
 * External dependencies
 */
import qs from 'query-string';

/**
 * WordPress dependencies
 */
import { sprintf, __ } from '@wordpress/i18n';
import { useState, useEffect } from '@wordpress/element';
import { Button, Card, CardBody, CardHeader } from '@wordpress/components';

/**
 * Internal dependencies
 */
import StatusIndicator from '@components/StatusIndicator';

const statuses = {
    loading: __('Clearing cache...', 'social-image-generator'),
    success: __('Cache cleared successfully!', 'social-image-generator'),
    error: __('Something went wrong, please try again later.', 'social-image-generator')
};

const CachePanel = () => {
    const [status, setStatus] = useState('idle');
    const [count, setCount] = useState(socialImageGenerator.cacheDirectoryCount);

    useEffect(() => {
        socialImageGenerator.cacheDirectoryCount = count;
    }, [count]);

    const clearCache = async () => {
        if (status === 'loading') {
            return;
        }

        setStatus('loading');

        const url = qs.stringifyUrl({
            url: `${socialImageGenerator.apiUrl}/clear-cache`,
            query: {
                _wpnonce: socialImageGenerator.nonce
            }
        });

        try {
            const response = await fetch(url);

            setStatus(response.status === 200 ? 'success' : 'error');

            if (response.status === 200) {
                setCount(0);
            }
        } catch (e) {
            setStatus('error');
        }
    };

    return (
        <Card className="sig-panel">
            <CardHeader>{__('Cache', 'social-image-generator')}</CardHeader>
            <CardBody>
                <div className="sig-row">
                    <div className="sig-row__title">{__('Image Folder', 'social-image-generator')}</div>
                    <div className="sig-row__content">
                        <code>{socialImageGenerator.cacheDirectory}</code>
                        <StatusIndicator
                            statuses={{
                                success: __('Everything is working correctly!', 'social-image-generator'),
                                error: __(
                                    "Directory not writable! This means Social Image Generator won't be able to function correctly. Please contact your host and ask them to resolve this issue.",
                                    'social-image-generator'
                                )
                            }}
                            currentStatus={socialImageGenerator.cacheDirectoryWritable ? 'success' : 'error'}
                        />
                        <div class="sig-row__label">
                            {sprintf(
                                __(
                                    'This is where Social Image Generator saves all generated images. Number of images saved: %d',
                                    'social-image-generator'
                                ),
                                count
                            )}
                        </div>
                    </div>
                </div>
                <div className="sig-row">
                    <div className="sig-row__title">{__('Refresh Images', 'social-image-generator')}</div>
                    <div className="sig-row__content">
                        <div className="sig-row__actions">
                            <Button
                                className="sig-row__button"
                                isSecondary
                                disabled={status === 'success'}
                                onClick={() => {
                                    clearCache();
                                }}
                            >
                                {__('Clear Cache', 'social-image-generator')}
                            </Button>
                            {status !== 'idle' && <StatusIndicator statuses={statuses} currentStatus={status} />}
                        </div>
                        <div class="sig-row__label">
                            {__(
                                'Social Image Generator automatically generates new images when necessary and saves them in the folder above. If something went wrong, you can clear the cache manually to regenerate all images.',
                                'social-image-generator'
                            )}
                        </div>
                    </div>
                </div>
            </CardBody>
        </Card>
    );
};

export default CachePanel;
